<template>
  <ik-data-table
    ref="zbon"
    :use-default-action="false"
    :model="Zbon"
    :use-select="true"
    :entity-name="$t('Zbon列表')"
    :icon="'mdi-format-list-bulleted'"
    :use-customer-action-only="true"
  >
    <template v-slot:footer>
      <v-btn
        class="mr-2"
        color="success"
        dark
        @click="outputPdf()"
      >
        {{ $t('PDF') }}
      </v-btn>
      <v-btn
        class="mr-2"
        color="primary"
        dark
        @click="print()"
      >
        {{ $t('打印') }}
      </v-btn>
    </template>
    <template v-slot:item.action="{ item }">
      <v-icon
        class="mr-2"
        small
        @click="printSingleItem(item)"
      >
        mdi-printer
      </v-icon>
      <v-icon
        class="mr-2"
        small
        @click="generateZBonPDF(item)"
      >
        mdi-file-pdf-box
      </v-icon>
    </template>
  </ik-data-table>
</template>

<script>

  import { PrinterAllZbon, reprintZbon } from '@/components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'
  import Zbon from '../../model/Zbon'
  import { IkDataTable } from 'metaflow-js'
  import GlobalConfig from '../../GlobalSettings'

  export default {
    components: { IkDataTable },
    data: function () {
      return ({
        Zbon: Zbon,
      })
    },
    methods: {
      print () {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie alle Datensätze drucken?', () => {
          PrinterAllZbon().then(() => {
            Utils.toast('Erfolgreich drucken!')

            this.$refs.zbon.reload()
          })
        })
      },
      async generateZBonPDF (item) {
        window.open(GlobalConfig.getEndPoint() + 'ZBon.php?op=exportPDF&id=' + item.id)
        // await hillo.get('', { id: item.id })
      },
      async outputPdf () {
        const selectedItemIds = this.$refs.zbon.$data.selectedItems.reduce((arr, i) => {
          arr.push(i.id)
          return arr
        }, []).join(',')
        console.log(selectedItemIds)
        this.Zbon.outputMultiplePDF(selectedItemIds)
      },

      printSingleItem (item) {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie dieser Rekord drucken?', () => {
          reprintZbon(item.id).then(() => {
            Utils.toast('Erfolgreich drucken!')
            this.$refs.zbon.reload()
          })
        })
      },
    },
  }
</script>
